.about__container {
  padding: 4rem 2rem;
  background-color: var(--neutral-light);
  position: relative;
  z-index: 1;
}
.about__intro {
  text-align: center;
  font-size: 1.2rem;
  color: var(--neutral-dark);
  line-height: 1.8;
  width: 80%;
  margin: 0 auto;
  margin-bottom: 3rem !important;
}

.highlight {
  font-weight: bold;
  color: var(--primary-dark);
}

.highlight-special {
  font-family: "Fira Code", monospace;
  letter-spacing: 0.03em;
}
.inline-link {
  color: var(--primary-dark);
  text-decoration: none;
  font-weight: 600;
  border-bottom: 1px dotted var(--primary-dark);
  transition: color 0.2s ease, border-bottom 0.2s ease;
}

.inline-link:hover {
  color: var(--primary);
  border-bottom: 1px solid var(--primary);
}

.about__title {
  text-align: center;
  font-size: 2.5rem;
  color: var(--primary);
  margin-bottom: 1rem;
  z-index: 1;
}

.about__timeline {
  position: relative;
  padding: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  max-width: 80%;
  margin: 0 auto;
}

.timeline__spiral {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: calc(100% + 50px); /* Extend to cover all items */
}

.timeline__spiral path {
  fill: none;
  stroke: var(--primary-dark);
  stroke-width: 2px;
  stroke-dasharray: 6, 6; /* Optional dashed effect */
  z-index: -1 !important;
}

.timeline__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  margin: 1.5rem 0;
  width: 80%;
  z-index: 1;
}

.timeline__item.left {
  align-self: flex-start;
  text-align: left;
  z-index: 1;
}

.timeline__item.right {
  align-self: flex-end;
  text-align: left;
  z-index: 1;
}

.timeline__circle {
  width: 60px;
  height: 60px;
  background-color: var(--neutral-light);
  border-radius: 50%;
  overflow: hidden; /* Ensure the image stays within the circle */
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  position: absolute;
  z-index: 1;
  top: -30px;
}

.timeline__circle img {
  width: 100%;
  height: auto;
  max-height: 100%;
  padding: 0.5rem;
  object-fit: contain; /* Ensures the image scales properly without cropping */
}

.timeline__content {
  background: var(--white);
  opacity: 95%;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  width: 90%;
  color: red;
}

.timeline__content h3 {
  font-size: 1.5rem;
  color: var(--primary-dark);
  margin-bottom: 0.5rem;
  padding-top: 20px; /* Ensure space for the circle */
}

.timeline__date {
  font-size: 1rem;
  color: var(--neutral-dark);
  margin-bottom: 1rem;
}

.timeline__description {
  font-size: 1rem;
  color: var(--neutral-dark);
  margin-bottom: 0.5rem;
}

.timeline__details {
  font-size: 0.9rem;
  color: var(--neutral);
}

.timeline__item:nth-child(odd) .timeline__content {
  margin-left: 50px;
}

.timeline__item:nth-child(even) .timeline__content {
  margin-right: 50px;
}
